$image-margin: 3rem;

.about-title h1 {
  padding-bottom: 1px;
  font-size: 30px;
  color: #555;  
}

.about-subtitle {
  text-align: center;
  color: #555;
}

.margin-top-40 {
  margin-top: 40px;
}

.anketa {
    position: relative;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}

.wrapper-beamodel {
  margin-bottom: 10px;
}


.position-relative {
  position: relative;
}

.form-group {
  margin-bottom: 15px;
}

.control-label {
  margin-bottom: 15px;
  font-weight: 700;
  text-transform: uppercase;
}

.control-label {
  text-align: center;
  color: #999;
}

.form-horizontal {
  position: relative;
}

.form-horizontal::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(51,51,51, 0.9) url(../../img/giphy.gif) center / 100px no-repeat;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease 0s;
}

.form-horizontal._sending::after {
  opacity: 1;
  visibility: visible;
}

#beAModel .form-control {
  border: 0px;
  border-bottom: 1px solid #ccc;
}

.form-control {
  border: 5px;
  border-bottom: 1px solid #ccc;
  width: 100%;
}



.form-control {
  box-shadow: none;
  border-radius: 0;
  display: block;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  background-image: none;
}


.wrapper-beamodel {
  padding-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form-group  {
  display: flex;
  flex-direction: column;
  width: 50%;
}

span.error {
  display: none; 
  position: absolute;
  top: calc(50% - -8px);
  right: 0;
  padding-right: 15px;
  font-size: 14px;
}
// Раздел Фото

.row-photos {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  text-align: center;
} 

.model-photos h5{
  text-align: center;
  color: #555;
  margin-top: 5px;
  text-shadow: none;
  font-weight: normal;
}

.join-upload {
  position: relative;
  vertical-align: bottom;
  width: 100%;
  padding-bottom: 20px;
}

.join-image {
  position: relative;
  width: 100%;
  height: auto;
  cursor: pointer;
  min-height: 100px;
  margin-bottom: 10px;
  margin-right: 30px;
}

.join-image img {
  width: 75%;
  height: auto;
  opacity: 1;
  -webkit-transition: opacity 500ms ease;
  -moz-transition: opacity 500ms ease;
  -o-transition: opacity 500ms ease;
  -ms-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}

.join-image-overlay {
  position: absolute;
  top: 0;
  left: $image-margin;
  width: 75%;
  height: 99%;
  z-index: 3;
  background-color: transparent;
  background-color: rgba(183, 183, 183, 0.45);
  -webkit-transition: opacity 500ms ease;
  -moz-transition: opacity 500ms ease;
  -o-transition: opacity 500ms ease;
  -ms-transition: opacity 500ms ease;
  transition: opacity 500ms ease;
}

.join-image.is-loaded .join-image-overlay {
  opacity: 0;
}

.join-image-title {
  position: absolute;
  width: 100%;
  height: auto;
  left: 0;
  top: 75%;
  font-size: 14px;
  color: #444;
  line-height: normal;
  text-transform: uppercase;
  text-align: center;
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.file-upload {
  height: 28px;
  margin: 10px 0 0;
  position: relative;
}


.file-upload input[type='file'] {
  display: block !important;
  width: 100% !important;
  height: 28px !important;
  opacity: 0 !important;
  position: absolute;
  z-index: 10;
  cursor: pointer;
  top: 0;
  left: 0;
  color: #555;
}

input[type=file] {
  display: block;
}

.upload-dummy.upload-success {
  color: #008000;
  overflow: hidden;
  text-overflow: ellipsis;
}

.upload-dummy.placeholder {
  color: #999;
}

.upload-dummy {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 0;
  background-color: #fff;
  text-transform: uppercase;
  margin: 0;
  color: #999;
  overflow: hidden;
  text-align: center;
  margin-top: 10px;
  border: none;
}

div.file-upload span.error {
  top: -32px;
  left: 65px;
  background: rgba(255,0,0,.75);
  width: 60%;
  color: white;
  text-align: center;
  padding: 0;
  z-index: 3;
}

.button-upload {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding-top: 40px;
  padding-bottom: 30px;
}

.btn-u {
    background: var(--color-black);
}

.btn-u {
  border: 0;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  font-weight: 400;
  padding: 11px 20px 5px ;
  position: relative;
  background: var(--color-black);
  white-space: nowrap;
  display: inline-block;
  text-decoration: none;
}

.btn-u:hover {
  background: var(--color-rose);
  color: #fff;
}

@media screen and (max-width: 1201px) {
  .row-photos {
    display: flex;
    flex-direction: column;
  }
  .model-photo {
    display: flex;
    flex-direction: column;
  }
}


@media screen and (max-width: 1059px) {
  .form-group {
    width: 100%;
  }

  .file-upload {
    margin-bottom: 60px;
    
  }

}

@media screen and (max-width: 414px) {
  .join-image-overlay {
  left: 2.9rem;
}
}

@media screen and (max-width: 320px) {
  .join-image-overlay {
    left: 2.3rem;
  }
}

@media screen and (max-width: 400px) {
  .join-image-overlay {
    left: 2.7rem;
  }

}


