

.school {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 30px;
  
}

.school-content {
  flex-basis: 50%;
  margin-right: 50px;
}

.content__text {
  font-size: 18px;
}

.content__image img {
  width: 500px;
}

h1 {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.title-description {
  margin-bottom: 30px
}

@media screen and (max-width: 1765px) {
  .school {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  
}

@media screen and (max-width: 500px) {
  .content__text {
    margin-left: 60px;
    width: 300px;
  }
}
