
.contacts {   
  display: flex;
  justify-content: center;
  align-items: center
 
}

.contacts-content {
  display: flex;
}

.contacts-address {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 40px;
}


.contacts-map {
  padding-bottom: 10px;
}

.fa {
  margin-right: 5px;
  margin-bottom: 10px;
}

.fa i {
  margin-bottom: 5px;
  color: var(--color-black)

}

.fa i:hover {
  color:var(--color-rose);
}

.contacts-description li  {
  margin-bottom: 12px;
  
}

.contacts-description a {
  color: var(--color-black);
}

.contacts-description a:hover {
  color: var(--color-rose)
}

.contacts-content li i  {
  color: var(--color-rose);
}




@media screen and (max-width: 705px) {
  .contacts-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .contacts-map {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 350px;
    padding-right: 15px;
    padding-left: 15px;

  }
} 

