.container-about {
  width: 450px;
  margin: 0 auto;
}


.about {
  display: flex;
  justify-content: center;
}

.about-content {
  width: 1000px;
  display: flex;
  justify-content: center;
}

.about-description {
  width: 1000px;
  position: relative;
  min-height: 1px;
  padding-left: 70px;
  color: var(--color-brown);
  font-size: 18px;

}

@media screen and (max-width: 760px) {
  .about-content {
  width: 400px;
  display: flex;
  padding-right: 15px;
  padding-left: 15px;
  align-items: center;
  flex-direction: column;
  }
  

}
.about-description {
  max-width: 440px;
  margin-right: 50px;
}