.teachers-description {
  display: flex;
  font-size: 18px;
}

.teacher-image {
  cursor: pointer;
  overflow:hidden;
}


.teacher-image {
  transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  -webkit-transition: all 1s ease-out;
}

.teacher-image:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  -o-transform: scale(1.1);
}

.teacher-title {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 60px ;
}

.teacher-title {
  font-size: 35px;
}
.img {
  width:350px;
  height:350px;
}


.teacher1:nth-child(5n+1) {
  margin-left: 0;
}

.teachers-description {
  flex-wrap: wrap;
}

.teacher1 {
  flex: 1;
  max-width: 19%;
  min-width: 19%;
  margin-left: 1.2%;
  margin-top: 1rem;
  margin-bottom: 2rem;
  text-align: center;
}

@media screen and (max-width: 1765px) {
   .teacher1{
    margin-left: 1%;
    width: 49%;
    max-width: 49%;
    min-width: 49%;
  }
  .teacher1:nth-child(5n+1){
    margin-left: 1%;
  }
  
  .teacher1:nth-child(2n+1){
    margin-left: 0;
  }
  
}




