.owl-carousel {
  width: 100%;
  height: 100%;

}

.swiper-slide  {
  margin-top: 20px;
  text-align: center;
  align-items: center;
  font-size: 18px;
  background: #fff;

  
  


  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}



@media screen and (max-width: 760px) {
  .swiper-slide img   {
    width: 90% !important;
    height: 90% !important;
    
  }

}

