footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
}
footer p {
  margin-left: 20px;
  margin-right: 20px;
  
}

.phone-footer{
  margin-top: 10px;
  color: var(--color-white);
}


footer {
  margin-top: 1rem;
  height: 3rem;
  background-color:var(--color-black);
  letter-spacing: .1rem
}

@media screen and (max-width: 450px) {
  footer {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    height: 100%;
}
}

@media screen and (max-width: 514px) {
  footer {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    height: 100%;
}
}
