.text-1 {
  margin-bottom: 10px;
}

ul.accordion-list {
  position: relative;
  display: block;
  width: 100%;
  height: auto;
  padding: 20px;
  margin: 0;
  list-style: none;
  background-color: #f9f9fA;
  
  li {
    position: relative;
    display: block;
    width: 100%;
    height: auto;
    background-color: #FFF;
    padding: 20px;
    margin: 0 auto 15px auto;
    border: 1px solid #eee;
    border-radius: 5px;
    cursor: pointer;
    
    &.active {
      h3 {
        &:after {
          transform: rotate(45deg);
        }
      }
    }
    
    h3 {
      font-weight: 700;
      position: relative;
      display: block;
      width: 100%;
      height: auto;
      padding: 0 0 0 0;
      margin: 0;
      font-size: 14px;
      letter-spacing: 0.01em;
      cursor: pointer;
      
      &:after {
        content: "+";
        position: absolute;
        right: 0;
        top: 0;
        color:var(--color-black);
        transition: all 0.3s ease-in-out;
        font-size: 20px;
      }
    }
    
    div.answer {
      position: relative;
      display: block;
      width: 100%;
      height: auto;
      margin: 0;
      padding: 0;
      cursor: pointer;
      
      p {
        position: relative;
        display: block;
        font-weight: 300;
        padding: 10px 0 0 0;
        cursor: pointer;
        line-height: 150%;
        margin: 0 0 15px 0;
        font-size: 16px;
      }
    }
  }
}

.btn-courses {
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: 2px solid var(--color-black);
  border-radius: 0.6em;
  color: var(--color-black);
  cursor: pointer;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  -webkit-align-self: center;
      -ms-flex-item-align: center;
          align-self: center;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  margin-top: 20px;
  padding: 20px 30px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
.btn-courses:hover{
  color: var(--color-white);
  outline: 0;
}

.first {
  -webkit-transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
  transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}
.btn-courses:hover {
    box-shadow: 0 0 40px 40px var(--color-black) inset;
  }


@media screen and (max-width: 1883px) {

    .content-wrapper{
    margin-left: 1%;
    width: 49%;
    max-width: 49%;
    min-width: 49%;
   }

    .content-wrapper:nth-child(4n+1){
      margin-left: 1%;
}

    .content-wrapper:nth-child(2n+1){
      margin-left: 0;
}

}

@media screen and (max-width: 630px) {
  .main-content {
    display: flex;  
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;

    ul.accordion-list li h3 {
      font-size: 14px;
  
    }
  }

  ul.accordion-list  {
      width: 270px;
      display: flex;
      justify-content: center;
      align-self: center;
  }


  .content-wrapper{
    display: flex;
    flex-direction: column;
  }




}


   

