

.flex {
  display: flex;
}

.f-col {
  flex-direction: column;
}

.wrap {
  flex-wrap: wrap;
}

.align-items-center {
  align-items: center;
}

.justify-content-between {
  justify-content: space-between;
}

.justify-content-center {
  justify-content: center;
}

/* Preset Classes */

// Model //

.wrapper {
  margin-top: 3rem;
  margin-bottom: 2rem;
}

  
.model {
  flex: 1;
  max-width: 19%;
  min-width: 19%;
  margin-left: 1.2%;
  margin-top: 1rem;
  text-align: center;
  transition: all .3s ease-in-out;
}

.model__wrapper {
  width: 100%;
  position: relative;
}

.model__detail {
  opacity: 0;
  transition: all .3s ease-in-out;
}

.detail__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-black);
  opacity: .6;
  transition: all .3s ease-in-out;
}

.detail__infos {
  position: absolute;
  width: 100%;
  height: 100%;
}

.detail__infos p {
  color: var(--color-white);
  margin: .2rem 0;
}

.model:hover {
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
  transform: scale(1.05);
  z-index: 2;
}

.model:hover .model__detail {
  opacity: 1;
  transition: all .3s ease-in-out;
}

.model__picture {
  border: 1px solid var(--color-secondary);
  width: 100%;
  height: 400px;
  transition: all .3s ease-in-out;
}

.model__picture img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  max-height: 100%;
}

.model:nth-child(5n+1) {
  margin-left: 0;
}

.model__fullname {
  font-family: 'Open Sans', sans-serif;
  letter-spacing: .05rem;
  text-align: center;
  font-weight: 400;
  font-size: 36px;
}

.model__name {
  background-color: var(--color-secondary);
  color: var(--color-black);
  padding: .5rem 0;
  font-size: 1rem;
  font-family: 'Open Sans', sans-serif;
  letter-spacing: .05rem;
  font-weight:400!important;
}

.model__details {
  margin: 2rem 0;
}

.model__details p {
  padding: 0 1rem;
  border-right: 1px solid var(--color-black);
}

.model__details p:nth-child(1) {
  border-left: 1px solid var(--color-black);
}

.model__details-title {
  font-weight: 700;
}

@media screen and (max-width: 768px) {

  .container {
      width: 90%;
  }
  .model {
    margin-left: 1%;
    width: 49%;
    max-width: 49%;
    min-width: 49%;
}


.model:nth-child(5n+1){
    margin-left: 1%;
}

.model:nth-child(2n+1){
    margin-left: 0;
}

.detail__wrapper {
    width: 100%;
}

.model__details p {
    border-right: 0;
    margin: 0.2rem 0;
}

.model__details p:nth-child(1) {
    border-left: 0;
}

.model__details.flex {
    flex-direction: column;
    align-items: center;
}

.model__picture {
    min-width: 100%;
    width: 100%;
    height: auto;
    height: 400px;
}

.model__picture img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    max-height: 100%;
}

.model__name{
    font-size: 1rem;
}

}





