
.header {
  position: relative;
  width: 100%;
  position: fixed;
  height: 100px;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-white);
  transition: height .3s ease-in
}

.header.fixed {
    border-bottom: 1px solid var(--color-secondary);
    width: 100%;
    position: fixed;
    top :0;
    height: 70px;
  }
.logo img{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo a {
  display: block;
}

.logo a img {
  display: block;
  width: 250px;
}

.header__inner{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
}

.menu {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;
  text-align: right;
  z-index: 1;
  transition: 0.5s;
}


.menu li a {
  color: var(--color-black);
  text-decoration: none;
  display: block;
  padding: 15px 10px;
  line-height: 1;
  transition: 0.3s;
}

.menu li a:hover {
  color: var(--color-rose);
}

.hamburger {
  position: relative;
  width: 30px;
  height: 4px;
  background: black;
  border-radius: 10px;
  cursor: pointer;
  z-index: 2;
  transition: 0.3s;
}

.hamburger:before,
.hamburger:after {
  content: "";
  position: absolute;
  height: 4px;
  right: 0;
  background: black;
  border-radius: 10px;
  transition: 0.3s;
   
}

.hamburger:before{
  top:  -10px;
  width: 20px;
}

.hamburger:after{
  top: 10px;
  width: 25px;
}

.toggle-menu {
  position: absolute;
  width: 30px;
  height: 100%;
  z-index: 3;
  cursor: pointer;
  opacity: 0;
}

.hamburger,
.toggle-menu {
 display: none; 
}

.navigation input:checked ~ .hamburger {
  background: transparent;
}

.navigation input:checked ~ .hamburger:before {
  top: 0;
  transform: rotate(-45deg);
  width: 30px;
}

.navigation input:checked ~ .hamburger:after {
  top: 0;
  transform: rotate(45deg);
  width: 30px;
} 

.navigation input:checked ~ .menu {
  right: 0;
  box-shadow: -20px 0 40p rgba(0,0,0,0.3);
}


@media screen and (max-width: 1630px) {
  .hamburger,
  .toggle-menu {
    display: block;
  } 

  .header {
    padding: 10px 20px;

  }

  .menu {
    position: absolute;
    justify-content: start;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 80px;
    right: -300px;
    background: var(--color-secondary);
    z-index: 2;
    height:80%;

  }

  .menu li {
    width: 100%;
  }

  .menu li a {
    padding: 12px 20px;
    font-size: 16px;
  }

  
}
// header //


